import Vue from 'vue'
import Vuex from 'vuex'
import auth from './auth'
import info from './info'
import VueIdb from '../idb'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    error: null
  },
  mutations: {
    'DELETE_INDEXED_DB' () {},
    setError (state, error) {
      state.error = error
    },
    clearError (state) {
      state.error = null
    }
  },
  actions: {
    async getCurrency ({ commit }) {
      return { date: '2021-01-01', rates: { UAN: 22 } }
    }
  },
  getters: {
    error: s => s.error
  },
  modules: {
    ...VueIdb.modules,
    auth,
    info
  },
  plugins: [VueIdb.plugin]
})
