import Vue from 'vue'
import VueIdb from 'vue-idb'

Vue.use(VueIdb)

export default new VueIdb({
  database: 'coin_main',
  schemas: [
    { files: 'pk++,name' },
    { files2: 'pk++,name' },
    { files3: 'id++,name' },
    { files4: 'id++,name' }
  ],
  options: {
    files: { type: 'list', primary: 'pk', label: 'name' },
    files2: { type: 'list', label: 'name' },
    files3: { type: 'list', label: 'name' },
    files4: { type: 'list', primary: 'id', label: 'name' }
  }
})
